export default {
	data() {
		return {
			payType: 1,
			orderInfo: {},
			totalMoney:0,
			orderId:''
		};
	},
	computed: {
	
	},
	onLoad(options) {
		this.totalMoney = options.totalMoney;
		this.orderId = options.orderId;
		console.log("orderId", orderId);
	},

	methods: {
		//确认支付
		confirm: async function() {
			var condition = {
				"orderId": this.orderId
			}
			this.$minApi.uniapp_post(this.$base_api.pay, condition).then(res => {
				if(res.success){
					uni.redirectTo({
						url: '/pages/money/paySuccess'
					})
				}
			})
		},
	}
}
